body {
  margin: 0;
  background-color: #121212;
  font-family: 'Noto Sans', 'Noto Sans Display', 'Quicksand', sans-serif;
}

.settings-container {
  padding: 30px;
  max-width: 290px;
  margin: 30px auto;
  color: #e0e0e0;

  background-color: rgba(30, 30, 30, 0.315); /* полупрозрачный фон */
  backdrop-filter: blur(30px);             /* размытие фона */
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.8),
              0 0 2px rgba(255, 255, 255, 0.6);

  overflow-y: auto;
  max-height: 80vh;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.settings-container::-webkit-scrollbar {
  display: none;
}



.settings-heading {
  margin-bottom: 30px;
  font-size: 1.8rem;
  text-align: center;
}

.settings-section-title {
  font-size: 1.2rem;
  margin: -5px 0 10px;
  color: #f0f0f0;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.settings-card {
  padding: 10px;
  border-radius: 16px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.settings-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  font-size: 1rem;
  background-color: rgba(30, 30, 30, 0.6); /* полупрозрачный фон */
  backdrop-filter: blur(8px);             /* размытие фона */
  color: #e0e0e0;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
  letter-spacing: 1px;
}

.settings-button:hover {
  background-color: #2a2a2a;
}

.settings-button-content {
  display: flex;
  align-items: center;
  gap: 10px;
}

.settings-icon {
  font-size: 1.2rem;
  color: #6FCF00; /* яркий зелёный */
}

.arrow-icon {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.4);
}

.credits-final {
  margin-top: 60px;
  text-align: center;
  font-size: 0.85rem;
  color: #c9c9c9;
  padding: 0 20px 30px;
  line-height: 1.7;
  font-weight: 400;
  opacity: 0.85;
}

.credits-title {
  font-weight: bold;
  font-size: 1rem;
  color: #ff9de6;
  margin-bottom: 10px;
}

.credits-line {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 4px 10px;
  margin-bottom: 8px;
}

.credits-line i {
  color: #999;
  margin-left: 4px;
}

.credits-tag {
  color: #f3a4e0;
  font-weight: 600;
  margin-right: 6px;
}

.profile-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 40px auto 25px;
  padding: 16px 20px;
  width: 65%;
  background-color: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5),
              0 0 1px rgba(255, 255, 255, 0.1);
              border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.profile-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 2px solid #ddd;
}

.profile-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.profile-name {
  font-size: 16px;
  color: #f0f0f0;
  font-weight: 500;
}

.profile-role {
  font-size: 13px;
  color: #bbb;
}

.profile-button {
  margin-left: auto;
  padding: 6px 14px;
  background-color: #6FCF00;
  color: #121212;
  text-decoration: none;
  border-radius: 15px;
  font-size: 13px;
  font-weight: 600;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 0 0 transparent;
}




