.footer-buttons {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #121212;
  border-top: 0.5px solid var(--border-color, #b4b6b48a);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 8px 0;
  z-index: 1000;
}

.menu-button {
  background-color: transparent;
  border: none;
  color: #6c757d;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  padding: 15px 0;
  cursor: pointer;
  transition: color 0.3s;
}

.menu-button .menu-icon {
  font-size: 24px;
  margin-bottom: 2px;
}

.menu-button.active {
  color: #6FCF00; /* активная кнопка */
}

.menu-button:focus {
  outline: none;
}
