/* === Базовая настройка body === */
body {
  margin: 0;
  background: #121212;
  font-family: -apple-system, BlinkMacSystemFont, ...;
  color: #e0e0e0;
  letter-spacing: 0.2px;
  line-height: 1.5;
}

/* === Основная обёртка === */
.otes-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  user-select: none;
  pointer-events: auto;
  -webkit-touch-callout: none;
}

.otes-wrapper * {
  user-select: none !important;
  -webkit-touch-callout: none !important;
}

/* === Заголовок блока и даты === */
.otes-title,
.otes-date-left {
  padding-left: 30px;
  text-align: left;
  font-size: 1.5rem;
  margin-bottom: 5px;
  color: #fff;
  letter-spacing: 1.5px;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

/* === Сетка карточек === */
.otes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 14px;
}

/* === Общая карточка === */
.otes-card {
  position: relative;
  background: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 14px;
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s, box-shadow 0.2s;
}

.otes-card:first-child .otes-card-title {
  border: none !important;
}

/* === Заголовок карточки === */
.otes-card-title {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: -8px;
  border-bottom: 1px solid #ffffff22;
  padding-bottom: 4px;
}

/* === Значения и юниты === */
.otes-value {
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 10px;
}

.unit {
  font-size: 0.75em;
  font-weight: normal;
  opacity: 0.8;
}

/* === Цвета === */
.otes-green,
.otes-yellow,
.otes-blue,
.otes-red {
  color: inherit;
}

.otes-yellow { color: #FFD54F; }
.otes-blue { color: #4FC3F7; }
.otes-red   { color: #EF5350; }

/* === Прогресс-бары === */
.otes-progress {
  width: 100%;
  height: 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  margin-top: 10px;
}

.otes-progress-bar {
  height: 100%;
  border-radius: 8px;
  transition: width 0.4s ease-in-out;
}

.otes-progress-bar.otes-yellow {
  background: linear-gradient(90deg, #facc15, #fde047);
}

.otes-progress-bar.otes-green {
  background: linear-gradient(90deg, #22c55e, #86efac);
}

.otes-progress-bar.otes-red {
  background: linear-gradient(90deg, #ef4444, #f87171);
}

/* === Информация под прогрессом === */
.otes-progress-info {
  margin-top: 10px;
  font-size: 14px;
}

/* === Статус бейджи === */
.otes-status-badge {
  margin-top: 12px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85rem;
  font-weight: bold;
  padding: 6px 10px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.08);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.otes-status-badge.otes-grey {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.4);
  padding: 4px 10px;
  border-radius: 25px;
  font-size: 12px;
  margin-top: 8px;
}

.otes-status-badge.otes-grey i {
  color: #888;
  font-size: 14px;
  margin-right: -2px !important;
  vertical-align: middle;
}


/* === Цитата === */
.otes-quote {
  text-align: center;
  margin-top: 50px;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.4);
  font-style: italic;
  letter-spacing: 0.5px;
}

/* === Инпуты === */
.otes-inputs {
  margin-bottom: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
}

.otes-input-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 12px;
}

.otes-input-group label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.otes-input-group input {
  padding: 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.07);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 1rem;
  outline: none;
  transition: 0.2s;
}

.otes-input-group input:focus {
  border-color: #6FCF00;
  background: rgba(255, 255, 255, 0.1);
}

.otes-input-card .otes-inputs {
  margin-top: 20px;
}

/* === Навигация по месяцам === */
.otes-date-range {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 14px;
  font-weight: 500;
}

.otes-arrow-button {
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 0;
}

.otes-nav-buttons {
  display: flex;
  gap: 8px;
}

.otes-nav-buttons button {
  background: none;
  color: #fff;
  font-size: 18px;
  border: none;
  cursor: pointer;
  padding: 4px 10px;
  border-radius: 8px;
  transition: 0.2s;
}

/* === Иконка в углу === */
.otes-icon-corner {
  position: absolute;
  bottom: 8px;
  right: 8px;
  font-size: 28px;
  color: #ccc;
}

/* === Слайд-панель (чаевые/процент) === */
.otes-slide-panel {
  position: relative;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-10px);
  transition: max-height 0.4s ease, opacity 0.4s ease, transform 0.4s ease;
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-top: 0;
}

.otes-slide-panel.open {
  max-height: 1000px;
  opacity: 1;
  transform: translateY(0);
}

/* === Плюс-кнопка === */
.otes-add-tip-btn {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.otes-plus-icon {
  font-size: 44px;
}

/* === Обёртка слайдера === */
.otes-slider-wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}

/* === Оверлей и модалка === */
.otes-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(30, 30, 30, 0.822);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.otes-modal {
  background: rgb(30, 30, 30);
  border-radius: 20px;
  padding: 24px 32px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
  position: relative;
  max-width: 400px;
  width: 60%;
  text-align: center;
  color: #e2e8f0;
}

.otes-modal h3 {
  margin-bottom: 20px;
  font-size: 20px;
  color: #f1f5f9;
}

.otes-modal-buttons {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 12px;
}

.otes-modal-buttons button {
  padding: 12px 20px;
  font-size: 16px;
  background: rgba(255, 255, 255, 0.05);
  color: #f1f5f9;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
  letter-spacing: 1px;
  border: none;
  outline: none;
}

.otes-modal-buttons button:focus {
  outline: none;
  box-shadow: none;
}

.otes-modal-close {
  position: absolute;
  top: 10px;
  right: 12px;
  background: transparent;
  border: none;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
}

/* === Крестик на карточках (чаевые/процент) === */
.otes-close-card {
  position: absolute;
  top: 15px;
  right: 8px;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  transition: color 0.2s ease;
  z-index: 10;
}

.otes-stats-row {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  padding-top: 6px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

.otes-stat {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.otes-stat-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 2px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.otes-stat-value {
  font-size: 1.1rem;
  font-weight: 600;
  color: #e0e0e0;
}

.otes-stat-value.otes-green {
  color: #22c55e;
}

.otes-stat-value.otes-red {
  color: #ef4444;
}

.otes-goal-inline {
  margin-left: auto;
  margin-top: 10px;
  
  padding: 6px 12px;
  
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 500;
  color: #f0f0f0;
  display: inline-block;
  
  animation: fadeIn 0.4s ease-in-out;
  text-align: center;
}

.otes-goal-label {
  color: #aaa;
  margin-right: 6px;
  font-weight: 400;
}

.otes-goal-value {
  color: #fff;
  font-weight: 600;
}
